<template>
  <div class="container">
    <router-link to="/mychannels" class="back-btn">‚Üê</router-link>

    <div class="channel-header">
      <h1 class="channel-title">{{ group.username }}</h1>
      <div class="channel-info-grid">
        <div class="info-card">
          <div class="info-number">{{ group.subscribers }}</div>
          <div class="info-label">–ü–æ–¥–ø–∏—Å—á–∏–∫–∏</div>
        </div>
        <div class="info-card">
          <div class="info-number">{{ group.avg_views }}</div>
          <div class="info-label">–ü—Ä–æ—Å–º–æ—Ç—Ä—ã</div>
        </div>
      </div>
      <div class="channel-actions">
        <button class="action-btn secondary" @click="editGroup">–°–æ—Ö—Ä–∞–Ω–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏—è</button>
        <button class="action-btn primary" @click="toggleCatalog">
          {{ inCatalog ? '–£–±—Ä–∞—Ç—å –∏–∑ –∫–∞—Ç–∞–ª–æ–≥–∞' : '–î–æ–±–∞–≤–∏—Ç—å –≤ –∫–∞—Ç–∞–ª–æ–≥' }}
        </button>
      </div>
    </div>

    <div class="schedule-section">
      <div class="schedule-title">–†–∞—Å–ø–∏—Å–∞–Ω–∏–µ –ø—É–±–ª–∏–∫–∞—Ü–∏–π</div>
      <div class="schedule-grid" ref="grid">
        <!-- –ì–µ–Ω–µ—Ä–∞—Ü–∏—è —è—á–µ–µ–∫ —Ä–∞—Å–ø–∏—Å–∞–Ω–∏—è -->
      </div>
      <button class="action-btn primary" @click="saveSchedule" style="margin-top: 16px;">
        –°–æ—Ö—Ä–∞–Ω–∏—Ç—å —Ä–∞—Å–ø–∏—Å–∞–Ω–∏–µ
      </button>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue'
import { useAppStore } from '@/stores/app'
import { useRoute } from 'vue-router'

const appStore = useAppStore()
const route = useRoute()
const groupId = Number(route.params.id)

const group = reactive({
  username: '',
  subject_ids: [],
  country_id: null,
  language_id: null,
  ad_prices: {},
  subscribers: 0,
  avg_views: 0,
  all_slots_ids: [],
  busy_slots_ids: []
})

const inCatalog = ref(false)
const gridRef = ref(null)

const headers = () => ({
  'Content-Type': 'application/json',
  Authorization: 'Telegram-Init-Data ' + appStore.tgInitDataRow
})

function initSchedule() {
  const grid = gridRef.value
  grid.innerHTML = ''
  // –ú–æ–∂–Ω–æ —Å–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞—Ç—å —è—á–µ–π–∫–∏ –∞–Ω–∞–ª–æ–≥–∏—á–Ω–æ –ø—Ä–∏–º–µ—Ä—É
}

async function saveSchedule() {
  // —Å–æ–±–∏—Ä–∞–µ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–µ schedule_ids
  const scheduleIds = []
  gridRef.value.querySelectorAll('.schedule-cell.active').forEach(cell => {
    scheduleIds.push(cell.dataset.slotId)
  })
  try {
    await fetch(
      `${appStore.apiBaseUrl}/api/my/group/${groupId}/schedule`,
      {
        method: 'POST',
        headers: headers(),
        body: JSON.stringify({ schedule_ids: scheduleIds }),
        mode: 'cors'
      }
    )
    alert('–†–∞—Å–ø–∏—Å–∞–Ω–∏–µ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–æ')
  } catch (e) {
    console.error('–û—à–∏–±–∫–∞ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è —Ä–∞—Å–ø–∏—Å–∞–Ω–∏—è:', e)
  }
}

async function editGroup() {
  try {
    await fetch(
      `${appStore.apiBaseUrl}/api/my/group/${groupId}/edit`,
      {
        method: 'POST',
        headers: headers(),
        body: JSON.stringify({
          subject_ids: group.subject_ids,
          country_id: group.country_id,
          language_id: group.language_id,
          ad_prices: group.ad_prices
        }),
        mode: 'cors'
      }
    )
    alert('–ò–∑–º–µ–Ω–µ–Ω–∏—è —Å–æ—Ö—Ä–∞–Ω–µ–Ω—ã')
  } catch (e) {
    console.error('–û—à–∏–±–∫–∞ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è –≥—Ä—É–ø–ø—ã:', e)
  }
}

async function toggleCatalog() {
  try {
    inCatalog.value = !inCatalog.value
    await fetch(
      `${appStore.apiBaseUrl}/api/my/group/${groupId}/set_in_catalog`,
      {
        method: 'POST',
        headers: headers(),
        body: JSON.stringify({ in_catalog: inCatalog.value }),
        mode: 'cors'
      }
    )
  } catch (e) {
    console.error('–û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –∫–∞—Ç–∞–ª–æ–≥–∞:', e)
  }
}

onMounted(async () => {
  await appStore.initTelegram()
  const res = await fetch(
    `${appStore.apiBaseUrl}/api/my/group/${groupId}`,
    { headers: headers(), mode: 'cors' }
  )
  Object.assign(group, await res.json())
  // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –Ω–∞—á–∞–ª—å–Ω—ã–π —Å—Ç–∞—Ç—É—Å –∫–∞—Ç–∞–ª–æ–≥–∞, –µ—Å–ª–∏ –µ—Å—Ç—å
  initSchedule()
})
</script>